@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.5.0/font/bootstrap-icons.css);
body {
  margin: 0 auto;
  font-family: 'Roboto Condensed', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.upper{
    background-image: url(/ospevic_2/static/media/background001.51cc7f7a.png);
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 730px;
    position: relative;
}

.upper-content{
    margin: auto;
    width: 70%;
    position: relative;
}


.bottom-menu{
    position: absolute;
    display: flex;
    top: 620px;
    right: 650px;
    background-color: white;
    border-radius: 5px;
    box-shadow: 0px 0px 30px rgb(34, 34, 34);

}

.big-box{
    padding: 10px 30px;
    margin: auto;
    margin: 10px;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.5s;
}

.big-box:hover{
    background-color: rgb(60, 57, 82);
}

.big-box:hover, .big-box:hover i{
    color: white;
    text-shadow: 0px 0px 5px rgb(212, 210, 247);
}

.big-box i{
    font-size: 40px;
    color: darkcyan;
}

.big-box p{
    font-size: 20px;
}

.first{
    display: flex;
    justify-content: space-between;
    border-bottom: 1px dashed white;
    padding: 10px;
    
}

.logo{
    width: 65px;
    height: 65px;
    border-radius: 100px;
    display: flex;
    margin-left: 10px;
    margin-top: 10px;
    transition: all 0.3s ease;
}

.logo img{
    margin: auto;
    vertical-align: middle;
    width: 40px;
}

.links{
    display: flex;
    background-color: rgb(0, 0, 0, 0.3);
    border-radius: 5px;
    padding: 5px;
    padding-left: 10px;
}

.whatsapp i{
    color: white;
    font-size: 50px;
    margin-right: 15px;
    text-shadow: 0px 0px 5px black;
}

.phone{
    vertical-align: middle;
}

.phone p{
    color: white;
    margin: 0px;
    font-size: 20px;
    text-align: left;
    margin-right: 10px;
    margin-top: 5px;
    text-shadow: 0px 0px 5px black, 0px 0px 5px black;
}

.menu-box{
    margin: auto;
    display: flex;
    justify-content: center;
}

@media (prefers-reduced-motion: no-preference) {
    .logo {
      animation: App-logo-spin infinite 2s linear;
      animation-direction: alternate-reverse;
    }
  }
  
  @keyframes App-logo-spin {
    from {
      box-shadow: 0px 0px 5px black inset;
      border: 5px solid rgb(97, 96, 99);
      background-color: rgb(185, 210, 218);
    }
    to {
      box-shadow: 0px 0px 0px white inset, 0px 0px 20px white;
      border: 5px solid rgb(183, 182, 184);
      background-color: rgb(255, 255, 255);
    }
    
  }
.box{
    width: 10%;
    height: 50px;
    border-left: 1px dashed white;
    
}

.box p{
    cursor: pointer;
    color: white;
    margin-top: 10px;
    text-shadow: 0px 0px 5px black, 0px 0px 5px black;
}
